<template>
    <el-container>
        <el-main>
            <component :is="whetherToLogin" v-on:login="switcher"></component>
        </el-main>
    </el-container>
</template>

<script>
import register from './register.vue'
import loginform from './loginform.vue'


export default{
    components:{
        register,
        loginform,
    },
    data(){
        return{
            loginInfo:{
                username:'',
                pass:'',
            },
            whetherToLogin:'loginform',
        }
    },
    methods:{
        switcher(e){
            if(e){
                this.whetherToLogin='loginform'
            }else{
                this.whetherToLogin='register'
            }
        },
    }
}
</script>