import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../components/login/login.vue'
import home from '../components/interaction/home.vue'

Vue.use(VueRouter)

const routes = [{
    path:'/',
    component: login
},
{
    path:'/home',
    component:home,
}
]

const router = new VueRouter({
    routes
})

export default router