<template>
    <el-card>
        <el-row style="font-size:4em;margin-bottom:10vh">
            <center><span>FreeðŸš—Car</span></center>
        </el-row>
        <el-row>
            <el-col>
                <el-form :model="loginInfo" :rules="rules" status-icon ref="loginInfo">
                    <el-form-item prop="username">
                        <el-input v-model="loginInfo.username" placeholder="username"></el-input>
                    </el-form-item>
                    <el-form-item prop="pass">
                        <el-input v-model="loginInfo.pass" placeholder="password" type="password" autocomplete="off" ></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button class="loginbtn" type="primary" @click="submitForm('loginInfo')">Log in</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col style="text-align:right" :xs='24' :sm='24' :md='24' :lg='24' :xl='24'>
                <span class="registerBtn" @click="toRegister">click to create a new account</span>
            </el-col>
        </el-row>
    </el-card>
</template>

<script>
import axios from 'axios';
import qs from 'qs';
axios.defaults.withCredentials=true;

export default{
    data(){
        var validateName=(rule,value,callback)=>{
            if(value===''){
                callback(new Error("username is null"));
            }else{
                callback();
            }
        };
        var validatePass=(rule,value,callback)=>{
            if(value===''){
                callback(new Error("password is null"));
            }else{
                callback();
            }
        };
        return{
            loginInfo:{
                username:'',
                pass:'',
            },
            rules:{
                username:[
                    {validator:validateName, trigger:'blur'}
                ],
                pass:[
                    {validator:validatePass, trigger:'blur'}
                ]
            }
        }
    },
    methods:{
        submitForm(formInfo){
            let that=this;
            that.$refs[formInfo].validate((valid)=>{
                if(valid){
                    console.log('login info submit');
                    let payload = qs.stringify({username:that.loginInfo.username,pass:that.loginInfo.pass})
                    axios.post(that.$baseURL+'/login/',payload)
                        .then(function(response){
                            if(response.status===200){
                                console.log(response);
                                that.$session.start();
                                that.$session.renew(response.data);
                                that.$session.set('username',that.loginInfo.username);
                                that.$session.set('balance',response.data.balance);
                                that.$router.push('/home');
                                that.$notify.success({
                                    title: that.loginInfo.username,
                                    message:'successfully log in'
                                })
                            }
                        })
                        .catch(function(){
                            that.$notify.error({
                                title:'log in failed',
                                message:'check your information'
                            })
                        })
                }
            })
        },
        toRegister(){
            this.$emit('login', false);
        }
    }
}
</script>

<style scoped>
.loginbtn{
    width:100%
}
.registerBtn:hover{
    color:blue;
    cursor:pointer
}
</style>