<template>
    <el-container>
        <el-main>
            <el-container style="width:100%;margin:0 0 5vh 0;padding:0">
                <el-main>
                    <el-row :gutter="20" style="display:flex;justify-content: space-between;align-items: center;">
                        <el-col :span="3"><el-avatar :size="100" :src="circleUrl" ></el-avatar></el-col>
                        <el-col :span="12" offset="2" style="font-size:2em">
                            <span class="addmoney" @click="addmoney">ðŸ’Ž {{userInfo.balance}}</span>
                        </el-col>
                        <el-col :span="3">
                           <i class="el-icon-delete logoutbtn"  @click="logoutmenu" style="font-size:1.5em">Log out</i>
                        </el-col>
                    </el-row>
                </el-main>

            </el-container>
            
            <el-card class="bikecard" body-style="margin:0;padding:0">
                
            </el-card>
            
                <bike v-show="whetherShowbikes" v-for="bike in bikes" :key="bike.pk" v-bind:bikeinfo="bike" v-on:confirm="confirm"></bike>
            
            <el-drawer
                :visible.sync="drawer"
                :direction="direction"
                modal="false"
                size="80%"
                ref='bikedrawer'>
                <el-container style="display:flex;justify-content:center">
                    <el-header><center style="font-size:3em">ðŸš— {{this.bike_info.brand}}</center></el-header>
                    <el-main><center>Cost for this bike: {{this.bike_info.price}}</center></el-main>
                    <el-footer>
                        <center><el-button style="width:60%" @click="confirmRent" >confirm</el-button></center>
                        <div style="width:100%;height:30vh;margin-top:5vh;display:flex;justify-content:center">
                            <el-image
                                style="width: 80%; height: 30vh;clear:both"
                                src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"
                                fit="contain"></el-image>
                            
                        </div>
                    </el-footer>
                </el-container>
            </el-drawer>
            <el-dialog
                    title="log out?"
                    :visible.sync="dialogVisible"
                    >
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">cancel</el-button>
                    <el-button type="warning" @click="logout">log out</el-button>
                </span>
            </el-dialog>
            <el-card v-show="whetherShowback" style="height:50vh;" body-style="height:100%">
                <el-container style="display:flex;justify-content:center;align-content:space-around">
                    <el-main><center style="font-size:3em;">ðŸš— {{this.bike_info.brand}}</center></el-main>
                    <el-footer><center><el-button style="width:65%;font-size:1em;margin-top:10vh" @click="bike_back" type="success" round>Back</el-button></center></el-footer>
                </el-container>
            </el-card>
            
        </el-main>
        
        
    </el-container>
</template>

<script>
import bike from './bike.vue'
import axios from 'axios';
import qs from 'qs';
axios.defaults.withCredentials=true;

export default{
    components:{
        bike,
    },
    data(){
        return{
            bikes:[],
            circleUrl: "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
            userInfo:{
                'username':'',
                'email':'',
                'balance':0.00
            },
            drawer: false,
            dialogVisible: false,
            direction: 'btt',
            bike_info:{
                'pk':'',
                'price':1.0,
                'brand':'',
            },
            whetherShowbikes:true,
            whetherShowback:false,
        }
    },
    methods:{
        confirm(bike){
            this.drawer=true
            let that=this;
            that.bike_info['pk']=bike.pk;
            that.bike_info['price']=bike.fields.price;
            that.bike_info['brand']=bike.fields.brand;
        },
        confirmRent(){
            let that=this;
            let payload = qs.stringify({cost:that.bike_info.price,username:that.userInfo.username,pk:that.bike_info.pk})
            axios.post(that.$baseURL+'/rent/',payload)
                .then(function(response){
                    that.userInfo.balance = response.data
                    that.$notify.success({
                                    title: 'successfully rent',
                                    message:'you have balance '+String(that.userInfo.balance)
                                });
                    that.$refs.bikedrawer.closeDrawer();
                    that.whetherShowbikes=false;
                    that.whetherShowback=true
                }).catch(function(){
                    that.$notify.error({
                                    title: 'you have no enough money',
                                    message:'you have balance '+String(that.userInfo.balance)
                                });
                    
                })
        },
        getBikes(){
            let that=this;
            axios.get(that.$baseURL+'/get_bikes/')
                .then(function(response){
                    let data=[];
                    response.data.forEach(element=>{
                        data.push(element)
                    })
                    that.bikes=data
                })
        },
        bike_back(){
            let that=this;
            let payload=qs.stringify({pk:that.bike_info.pk})
            axios.post(that.$baseURL+'/rent_back/',payload)
                .then(function(response){
                    that.$notify.success({
                        title: 'successfully back',
                        message: response.data,
                    });
                    that.whetherShowbikes=true
                    that.getBikes()
                    that.whetherShowback=false
                })
        },
        logoutmenu(){
            this.dialogVisible=true;
        },
        addmoney(){
            let that=this
            let payload = qs.stringify({money:10,username:that.userInfo.username})
            axios.post(that.$baseURL+'/add_balance/', payload)
                .then(function(response){
                    that.$notify.success({
                        title: 'successfully add 10 money',
                        message: 'you have '+String(response.data),
                    });
                    that.userInfo.balance=response.data
                })
        },
        logout(){
            this.dialogVisible = false
            let that=this
            let payload=qs.stringify({username:this.userInfo.username})
            axios.post(that.$baseURL+'/logout/', payload)
                .then(function(){
                    that.$session.destroy()
                    that.$notify.success({
                        title: 'successfully logout',
                    });
                    that.$router.push('/');
                })
        }
    },
    mounted(){
        let that = this;
        that.getBikes();
        that.userInfo.balance=that.$session.get('balance')
    },
    beforeCreate:function(){
        let that=this

        if (!that.$session.exists()) {
            that.$router.push('/')
            that.$notify.error({
                title:'no log information registered',
                message:'you need to log in first'
            })
        }
  }
}
</script>

<style scoped>
.avatar:hover{
    box-shadow: 0 8px 12px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    cursor: pointer;
}
.addmoney:hover{
    color:aquamarine;
    cursor: pointer;
}
.logoutbtn:hover{
    color:aquamarine;
    cursor: pointer;
}


</style>