<template>
    <el-card>
        <el-row style="font-size:4em;margin-bottom:10vh">
            <center><span>Register</span></center>
        </el-row>
        <el-row>
            <el-col>
                <el-form :model="registerInfo" :rules="rules" status-icon ref="registerInfo">
                    <el-form-item prop="username">
                        <el-input v-model="registerInfo.username" placeholder="username"></el-input>
                    </el-form-item>
                    <el-form-item prop="pass">
                        <el-input v-model="registerInfo.pass" placeholder="password" type="password" autocomplete="off" ></el-input>
                    </el-form-item>
                    <el-form-item prop="mail">
                        <el-input v-model="registerInfo.mail" placeholder="email"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button class="registerbtn" type="primary" @click="submitForm('loginInfo')">Register</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col style="text-align:right" :xs='24' :sm='24' :md='24' :lg='24' :xl='24'>
                <span class="loginBtn" @click="toLogin">have a account? log in</span>
            </el-col>
        </el-row>
    </el-card>
</template>

<script>
import axios from 'axios';
import qs from 'qs';
axios.defaults.withCredentials=true;

export default{
    data(){
        var validateName=(rule,value,callback)=>{
            if(value===''){
                callback(new Error("username is null"));
            }else{
                callback();
            }
        };
        var validatePass=(rule,value,callback)=>{
            if(value===''){
                callback(new Error("password is null"));
            }else{
                callback();
            }
        };
        var validateMail=(rule,value,callback)=>{
            if(value===''){
                callback(new Error("email is null"));
            }else{
                var re=/([a-zA-Z0-9]+)@([a-zA-Z0-9]+)(\.)([a-zA-Z0-9]+)/;
                if(re.test(value)){
                    callback();//if use mailbox, we should distinguish them as different data format in database search.
                }else{
                    callback(new Error("format error"));
                }
            }
        };
        return{
            registerInfo:{
                username:'',
                pass:'',
                mail:'',
            },
            rules:{
                username:[
                    {validator:validateName, trigger:'blur'}
                ],
                pass:[
                    {validator:validatePass, trigger:'blur'}
                ],
                mail:[
                    {validator:validateMail, trigger:'blur'}
                ]
            }
        }
    },
    methods:{
        submitForm(){
            let that=this
            let payload=qs.stringify({username:that.registerInfo.username,pass:that.registerInfo.pass,mail:that.registerInfo.mail})
            console.log(that.registerInfo.username)
            axios.post(that.$baseURL+'/signup/', payload)
                .then(function(response){
                    that.$notify.success({
                        title: 'successfully register',
                        message: response.data
                    });
                    that.$router.push('/');
                })
                .catch(function(error){
                    
                    that.$notify.error({
                            title:'existing username',
                            message:error
                        })
                    
                })
        },
        toLogin(){
            this.$emit('login', true)
        }
    }
}
</script>

<style scoped>
.registerbtn{
    width:100%
}
.loginBtn:hover{
    color:blue;
    cursor:pointer
}
</style>